<template>
  <div>
    <div class="flex xl:flex-row flex-col justify-between mb-8">
      <div class="lg:flex lg:divide-x lg:divide-grey-01">
        <div class="flex items-center lg:pr-4 lg:mb-0 mb-4">
          <router-link :to="{ name: 'setting' }" class="text-dark mr-4"
            ><span class="material-icons">chevron_left</span></router-link
          >
          <h5>Item Sort Order</h5>
        </div>
        <div class="lg:pl-4 flex items-center">
          <Breadcrumb :items="breadcrumb">
            <span class="material-icons">chevron_right</span>
          </Breadcrumb>
        </div>
      </div>
    </div>
    <Card v-loading="loading.all">
      <template #header>
        <div class="flex flex-wrap items-center">
          <h6 class="text-xl mr-2">Item Sort Order</h6>
        </div>
      </template>
      <template #default>
        <div class="flex">
          <div class="w-1/2 p-2">
            <div class="flex items-center mb-6">
              <h2 class="text-lg mr-2">Main Item</h2>
              <Popover trigger="hover" width="400">
                <div class="break-words">
                  You can set up to 11 home page tiles
                </div>
                <template #reference>
                  <i
                    class="material-icons text-teal-04 cursor-pointer select-none"
                    >info</i
                  >
                </template></Popover
              >
            </div>
            <div
              class="overflow-y-auto border border-grey-01 rounded-xl bg-grey-lighten p-2"
              style="min-height: 80vh; max-height: 80vh"
            >
              <Draggable
                :list="selectedDevices"
                :swap-threshold="1"
                handle=".card-device__draggable"
                group="devices"
                class="flex flex-wrap"
              >
                <div
                  class="w-1/4 p-2"
                  v-for="(device, deviceIndex) of selectedDevices"
                  :key="deviceIndex"
                >
                  <div class="card-device">
                    <div class="card-device__draggable">
                      <i class="material-icons">open_with</i>
                    </div>
                    <div class="card-device__content">
                      <div
                        class="card-device__icon"
                        :style="`-webkit-mask-image: url('${device?.icon}')`"
                      ></div>
                      <div class="card-device__title">
                        <h6 class="text-xl font-medium">
                          {{ device.name }}
                        </h6>
                      </div>
                    </div>
                  </div>
                </div>
                <div
                  v-if="!isReachMaxSelected"
                  id="device-drop-area"
                  class="w-1/4 p-2 select-none"
                >
                  <div :class="['card-device', 'h-full', '--slot']">
                    <div
                      class="flex flex-col items-center justify-center text-grey-03"
                    >
                      <i class="material-icons icon-lg">add_circle</i>
                      <p class="text-lg text-center my-4 font-normal">
                        Drop Here
                      </p>
                    </div>
                  </div>
                </div>
                <div v-if="!isHideSellOtherDevice" class="w-1/4 p-2">
                  <div :class="['card-device', 'h-full', '--other']">
                    <div class="flex items-center justify-center h-full">
                      <h3 class="text-lg text-white font-normal">
                        Sell Other Item
                      </h3>
                      <i class="material-icons icon-md">chevron_right</i>
                    </div>
                  </div>
                </div>
              </Draggable>
            </div>
          </div>
          <div class="self-center mx-4">
            <i class="material-icons icon-md">swap_horiz</i>
          </div>
          <div class="w-1/2 p-2 flex flex-col">
            <div class="mb-6">
              <h2 class="text-lg mr-2">Other Items</h2>
            </div>
            <div
              class="flex flex-grow flex-col bg-grey-lighten border border-grey-01 rounded-xl p-2"
            >
              <div class="p-2">
                <Input
                  v-model="filters.search"
                  placeholder="Search Item Name"
                  block
                  icon
                >
                  <template #icon>
                    <i class="material-icons">search</i>
                  </template>
                </Input>
              </div>
              <div class="relative h-96 max-h-full flex-grow overflow-y-auto">
                <div v-if="isHideSellOtherDevice" class="absolute left-1/2 top-1/2 transform -translate-y-1/2 -translate-x-1/2">
                  <p class="text-lg">No device available</p>
                </div>
                <Draggable
                  v-loading="loading.categories"
                  :move="handleMoveDeviceOption"
                  @end="handleDragEnd"
                  :list="filteredOptionDevices"
                  :swap-threshold="1"
                  handle=".card-device__draggable"
                  group="devices"
                  class="flex flex-wrap"
                  :empty-insert-threshold="30"
                >
                  <div
                    class="w-1/4 p-2"
                    v-for="(
                      optionDevice, optionDeviceIndex
                    ) of filteredOptionDevices"
                    :key="optionDeviceIndex"
                  >
                    <div class="card-device">
                      <div class="card-device__draggable">
                        <i class="material-icons">open_with</i>
                      </div>
                      <div class="card-device__content">
                        <div
                          class="card-device__icon"
                          :style="`-webkit-mask-image: url('${optionDevice?.icon}')`"
                        ></div>
                        <div class="card-device__title">
                          <h6 class="text-xl font-medium">
                            {{ optionDevice.name }}
                          </h6>
                        </div>
                      </div>
                    </div>
                  </div>
                </Draggable>
              </div>
            </div>
          </div>
        </div>
      </template>
      <template #footer>
        <div class="flex justify-end">
          <Button
            @click="handleUpdate"
            :loading="loading.update"
            color="teal"
            view="smooth"
            >Save</Button
          >
        </div>
      </template>
    </Card>
  </div>
</template>

<script>
import { defineComponent, watch, ref, computed, reactive } from 'vue'
import { useWebsitePages } from 'composable/setting'
import Card from 'bbUi/components/card/card.vue'
import Input from 'bbUi/components/input/input.vue'
import Button from 'bbUi/components/button/button.vue'
import Pagination from 'components/Pagination.vue'
import Breadcrumb from 'bbUi/components/breadcrumb/breadcrumb.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import { VueDraggableNext } from 'vue-draggable-next'
import { ElMessage } from 'element-plus'

import debounce from 'utils/debounce'

export default defineComponent({
  components: {
    Card,
    Input,
    Button,
    Breadcrumb,
    Pagination,
    Modal,
    Draggable: VueDraggableNext
  },
  async setup(props, { emit }) {
    const {
      stateAll,
      stateCategories,
      loading,
      filters,
      actionAll,
      actionCategories,
      actionUpdate,
      modal
    } = useWebsitePages()
    const selectedDevices = ref([])
    const optionDevices = ref([])
    const draggedDeviceOptionContext = reactive({
      index: null,
      element: null,
      relatedElement: null,
      relatedContext: null
    })

    const breadcrumb = [
      {
        text: 'Settings',
        to: {
          name: 'setting'
        }
      },
      {
        text: 'Item Sort Order'
      }
    ]
    const editCategory = reactive({
      selectedIndex: null
    })
    const currentlySelectedCategories = computed(() =>
      selectedDevices.value.map((category) => category?.name)
    )
    const isReachMaxSelected = computed(
      () => selectedDevices.value.length == 11
    )
    const isEditCategory = computed(() => !!editCategory.selectedIndex)
    const filteredOptionDevices = computed(() => {
      if (
        Array.isArray(optionDevices.value) &&
        Array.isArray(selectedDevices.value)
      ) {
        const selectedDeviceNames = selectedDevices.value.map(
          (device) => device.name
        )
        return optionDevices.value.filter(
          (device) => !selectedDeviceNames.includes(device.name)
        )
      }
      return []
    })
    const isHideSellOtherDevice = computed(
      () => {
        return filteredOptionDevices.value.length === 0 &&
        !filters.search &&
        !loading.categories
      }
    )

    const handleSelectCategory = (category) => {
      const isEdit = isEditCategory.value
      if (isEdit) handleEditTile(category)
      else handleAddTile(category)
    }
    const handleAddTile = (category) => {
      if (isCurrentlySelected(category)) return null
      selectedDevices.value.push(category)
      modal.update = false
    }
    const handleEditTile = (category) => {
      const selectedIndex = editCategory.selectedIndex
      selectedDevices.value[selectedIndex] = category
      editCategory.selectedIndex = null
      modal.update = false
    }
    const handleDeleteTile = (categoryIndex) => {
      selectedDevices.value.splice(categoryIndex, 1)
    }
    const handleUpdate = async () =>
      await actionUpdate(selectedDevices.value, () => {
        ElMessage({
          type: 'success',
          message: 'Popular category/brand updated'
        })
      })
    const handleOpenModalAdd = () => {
      if (isReachMaxSelected.value) return null
      modal.update = true
    }
    const handleOpenModalEdit = (categoryIndex) => {
      editCategory.selectedIndex = categoryIndex
      modal.update = true
    }
    const handleCloseModalUpdate = () => {
      filters.search = null
      editCategory.selectedIndex = null
    }
    const resetDraggedDeviceOption = () => {
      Object.assign(draggedDeviceOptionContext, {
        index: null,
        element: null,
        relatedElement: null,
        relatedContext: null
      })
    }
    const handleDragEnd = (ev) => {
      const { relatedElement, relatedContext, element } =
        draggedDeviceOptionContext
      if (relatedElement === null) {
        resetDraggedDeviceOption()
        return null
      }
      // append selected item
      if (relatedElement.id === 'device-drop-area') {
        selectedDevices.value.push(element)
        resetDraggedDeviceOption()
        return
      }
      // swap selected item
      if (!!relatedContext.index && element !== null && relatedContext !== null) {
        if (relatedContext.element) {
          selectedDevices.value.splice(relatedContext.index, 1, element)
        }
      }
      // Reset dragged device option context state
      resetDraggedDeviceOption()
    }
    const handleMoveDeviceOption = (ev) => {
      if (ev.from !== ev.to) {
        Object.assign(draggedDeviceOptionContext, {
          ...ev.draggedContext,
          relatedElement: ev.related,
          relatedContext: ev.relatedContext
        })
      }
      return false
    }
    const isCurrentlySelected = (category) =>
      currentlySelectedCategories.value.includes(category.name)

    watch(
      () => stateAll.value,
      (data) => (selectedDevices.value = Array.isArray(data) ? data : [])
    )
    watch(
      () => stateCategories.value,
      (data) => {
        if (Array.isArray(data)) {
          optionDevices.value = data
        }
      }
    )

    watch(
      () => filters,
      debounce(async () => await actionCategories(), 200),
      {
        deep: true
      }
    )

    await Promise.all([actionAll(), actionCategories()])

    return {
      selectedDevices,
      filteredOptionDevices,
      editCategory,
      breadcrumb,
      loading,
      filters,
      modal,
      handleAddTile,
      handleOpenModalAdd,
      handleOpenModalEdit,
      handleDeleteTile,
      handleUpdate,
      handleSelectCategory,
      handleCloseModalUpdate,
      handleDragEnd,
      handleMoveDeviceOption,
      isCurrentlySelected,
      isReachMaxSelected,
      isEditCategory,
      isHideSellOtherDevice
    }
  }
})
</script>

